@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main__container {
    display: -webkit-flex;
    display: flex;
    position: relative;
    max-width: 1440px;
    min-width: 786px;
}

.sidebar__container {
    position: fixed;
    height: 100vh;
    width: 13em;
    background-color: #222831;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    transition: 0.5s;
    overflow: hidden;
    z-index: 11;
}

.sidebar__container__collapsed {
    position: fixed;
    height: 100vh;
    width: 4.1em;
    background-color: #222831;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    transition: 0.5s;
    overflow: hidden;
    z-index: 11;
}

.list__items {
     padding: 0;
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: column;
             flex-direction: column;
     list-style-type: none;
     -webkit-justify-content: space-between;
             justify-content: space-between;
}

.right__container__collapsed {
    margin-left: 13em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    transition: 0.5s;
    width: 100%;
    background: rgb(240, 240, 240) none repeat scroll 0% 0%;
}
.right__container {
    margin-left: 4.1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    transition: 0.5s;
    width: 100%;
    background: rgb(240, 240, 240) none repeat scroll 0% 0%;
}

.router__links {
    text-decoration: none;
    color: rgb(138, 135, 151);
    font-family: 'Nunito', sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    padding: 1.3em 1.3em 1.3em 1.5em;
    font-size: 1rem;
    transition: .2s;
}

.router__links:hover {
    color: white;   
}

.router__links:hover svg{
    fill: white;
}

.router__links svg{
    fill: rgb(138, 135, 151);
    width: 1.2rem;
    height: 1.2rem;
    padding-right: 1.5rem;
    transition: .2s;
}

.sidebar__top {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.list__items h4{
    color: rgb(6, 211, 247);
    padding: 1.3em 1.3em 1em 1.5em;
    font-family: 'Montserrat', sans-serif;
}

.sidebar__top:hover {
    color: white;
}

.active{
    color: white;
    font-weight: bold;
}

.active svg{
    fill: white;
}

.navbar {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    z-index: 10;
    -webkit-align-items: center;
            align-items: center;
    height: 3.5em;
    padding-right: 0.7em;
    background-color: white;
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.06),0px 8px 10px 1px rgba(0, 0, 0, 0.042),0px 3px 14px 2px rgba(0, 0, 0, 0.036);
}

.navbar__icons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.toggleArrow {
    margin-left: 0.5rem;
    transition: 0.5s;
}

.toggleArrow__rotated {
    margin-left: 0.5rem;
    transition: 0.5s;
}

.logo {
    visibility: hidden;
}

.loading {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
